import React from "react"
import { useRpjson } from "@rp/react/common/rpjson"
import { DocumentsCollectionRPJSONItem } from "@rp/react/tanstack-api/documents"
import { LoadingSpinner } from "@rp/react/web/common/base-components"
import { Box } from "@rp/react/web/mui/core"
import { DocumentViewerSelector } from "../DocumentViewerPage"
import { getPDFOffsets } from "../helpers/getPDFOffsets"

type DocumentsCollectionTabBodyProps = DocumentsCollectionRPJSONItem & {
  isVisible: boolean
  isFromChat: boolean
}

const DocumentsCollectionTabBody = ({
  isVisible,
  label,
  info,
  isFromChat,
}: DocumentsCollectionTabBodyProps) => {
  const documentId = info?.rpDocumentId || ""

  const { data, isLoading } = useRpjson(documentId, "public")

  if (!isVisible || !documentId) return null

  if (isLoading) return <LoadingSpinner />

  const { pdfHeaderOffset, pdfBodyOffset } = getPDFOffsets(isFromChat)

  return (
    <Box role="tabpanel" aria-label={label} sx={{ textAlign: "left" }}>
      <DocumentViewerSelector
        shouldUseScrollTrigger={false}
        isRpJsonLoading={false}
        rpjsonDocument={data}
        isPrivate={false}
        documentId={documentId}
        backTitle={""}
        disableBackButton
        disableCheckbox
        disableDocumentDiscovery={false}
        error={null}
        rpjsonHeaderOffset={-40}
        pdfHeaderOffset={pdfHeaderOffset}
        pdfBodyOffset={pdfBodyOffset}
      />
    </Box>
  )
}

export { DocumentsCollectionTabBody }
